
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { supabase } from "@/integrations/supabase/client";
import PageLayout from "@/components/layout/PageLayout";
import { Loader2, Timer, AlertTriangle, ExternalLink } from "lucide-react";
import { Button } from "@/components/ui/button";
import { formatDistanceToNowStrict, isBefore } from "date-fns";
import { toast } from "@/hooks/use-toast";

const SessionPage: React.FC = () => {
  const { "*": slug } = useParams();
  const [session, setSession] = useState<any>(null);
  const [status, setStatus] = useState<"loading" | "not_found" | "expired" | "valid">("loading");

  useEffect(() => {
    if (!slug) return;

    const fetchSession = async () => {
      try {
        // Query the session using ILIKE to find partial matches with the slug
        const { data, error } = await supabase
          .from("sessions")
          .select("*")
          .ilike("session_url", `%${slug}%`)
          .single();

        if (error || !data) {
          setStatus("not_found");
          return;
        }

        const isExpired = isBefore(new Date(data.expires_at), new Date());

        setSession(data);
        setStatus(isExpired ? "expired" : "valid");
      } catch (error) {
        console.error("Error fetching session:", error);
        toast({
          title: "Error",
          description: "Failed to load session data",
          variant: "destructive",
        });
        setStatus("not_found");
      }
    };

    fetchSession();
  }, [slug]);

  const handleOpenSession = () => {
    if (session?.session_url) {
      window.open(session.session_url, "_blank");
    }
  };

  const renderStatus = () => {
    if (status === "loading") {
      return (
        <div className="flex flex-col items-center justify-center py-24 text-center">
          <Loader2 className="animate-spin w-8 h-8 mb-4 text-white/80" />
          <p className="text-lg text-muted-foreground">Loading session...</p>
        </div>
      );
    }

    if (status === "not_found") {
      return (
        <div className="text-center py-24">
          <AlertTriangle className="w-12 h-12 text-red-400 mx-auto mb-4" />
          <h2 className="text-2xl font-bold mb-2">Session not found</h2>
          <p className="text-muted-foreground">This session doesn't exist or may have been deleted.</p>
        </div>
      );
    }

    if (status === "expired") {
      return (
        <div className="text-center py-24">
          <Timer className="w-12 h-12 text-yellow-400 mx-auto mb-4" />
          <h2 className="text-2xl font-bold mb-2">Session expired</h2>
          <p className="text-muted-foreground">This session is no longer available.</p>
        </div>
      );
    }

    const timeLeft = formatDistanceToNowStrict(new Date(session.expires_at));

    return (
      <div className="text-center py-24 space-y-6">
        <h1 className="text-3xl font-bold text-white">Session Active</h1>
        <p className="text-muted-foreground text-lg">
          Expires in <span className="font-semibold text-white">{timeLeft}</span>
        </p>
        <Button
          size="lg"
          className="bg-cartunlock-blue hover:bg-cartunlock-blue/90 flex items-center gap-2"
          onClick={handleOpenSession}
        >
          <ExternalLink size={18} />
          Open Session
        </Button>
      </div>
    );
  };

  return <PageLayout>{renderStatus()}</PageLayout>;
};

export default SessionPage;
