
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Button } from "@/components/ui/button";
import { User, LogOut, Settings, Home, LayoutDashboard, Clock } from "lucide-react";

const Navbar: React.FC = () => {
  const location = useLocation();
  const isLoggedIn = location.pathname !== '/login' && location.pathname !== '/';

  return (
    <header className="w-full py-4 px-6 flex justify-between items-center glass-card z-40">
      <div className="flex items-center gap-2">
        <div className="relative h-8 w-8 animate-pulse-glow">
          <div className="absolute inset-0 rounded-full bg-cartunlock-blue opacity-20"></div>
          <div className="absolute inset-1 rounded-full bg-cartunlock-background flex items-center justify-center">
            <div className="h-3 w-3 rounded-full bg-cartunlock-blue"></div>
          </div>
        </div>
        <Link to="/" className="text-xl font-bold text-white tracking-tight">
          Cart<span className="text-cartunlock-blue">Unlock</span>
        </Link>
      </div>
      
      {isLoggedIn ? (
        <div className="flex items-center gap-4">
          <nav className="hidden md:flex items-center gap-4 mr-4">
            <NavLink href="/dashboard" icon={<LayoutDashboard size={16} />}>
              Dashboard
            </NavLink>
            <NavLink href="/sessions" icon={<Clock size={16} />}>
              Sessions
            </NavLink>
            <NavLink href="/settings" icon={<Settings size={16} />}>
              Settings
            </NavLink>
          </nav>
          <Button variant="ghost" size="icon" className="text-cartunlock-text">
            <LogOut size={20} />
          </Button>
          <div className="h-8 w-8 rounded-full bg-cartunlock-blue flex items-center justify-center">
            <User size={16} className="text-white" />
          </div>
        </div>
      ) : (
        <div className="flex items-center gap-2">
          <Button asChild variant="ghost" className="text-cartunlock-text">
            <Link to="/login">Login</Link>
          </Button>
          <Button asChild className="bg-cartunlock-blue hover:bg-cartunlock-blue/80">
            <Link to="/login">Get Started</Link>
          </Button>
        </div>
      )}
    </header>
  );
};

interface NavLinkProps {
  href: string;
  children: React.ReactNode;
  icon?: React.ReactNode;
}

const NavLink: React.FC<NavLinkProps> = ({ href, children, icon }) => {
  const location = useLocation();
  const isActive = location.pathname === href;
  
  return (
    <Link 
      to={href}
      className={`flex items-center gap-1.5 px-3 py-1.5 text-sm rounded-md transition-all ${
        isActive 
        ? "text-cartunlock-blue bg-cartunlock-blue/10 font-medium" 
        : "text-cartunlock-text hover:bg-white/5"
      }`}
    >
      {icon}
      {children}
    </Link>
  );
};

export default Navbar;
