
import React, { useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import { Button } from "@/components/ui/button";
import { ArrowRight, ShoppingCart, Lock, Globe, Clock, Shield } from "lucide-react";
import PageLayout from "@/components/layout/PageLayout";

const HomePage: React.FC = () => {
  const heroRef = useRef<HTMLDivElement>(null);
  const howItWorksRef = useRef<HTMLDivElement>(null);
  const sitesRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add('animate-fade-in');
          }
        });
      },
      { threshold: 0.1 }
    );

    const sections = [heroRef.current, howItWorksRef.current, sitesRef.current];
    sections.forEach(section => {
      if (section) observer.observe(section);
    });

    return () => {
      sections.forEach(section => {
        if (section) observer.unobserve(section);
      });
    };
  }, []);

  return (
    <PageLayout fullWidth>
      {/* Hero Section */}
      <section 
        ref={heroRef}
        className="min-h-[80vh] flex flex-col justify-center relative px-4 md:px-12 lg:px-24 py-16 opacity-0"
      >
        <div className="absolute top-0 left-0 right-0 h-full w-full overflow-hidden z-[-1]">
          <div className="absolute top-[10%] left-[5%] w-72 h-72 rounded-full bg-cartunlock-blue opacity-10 filter blur-[80px]"></div>
          <div className="absolute bottom-[10%] right-[5%] w-80 h-80 rounded-full bg-cartunlock-green opacity-10 filter blur-[100px]"></div>
          <div className="absolute top-[40%] right-[30%] w-40 h-40 rounded-full bg-cartunlock-gold opacity-10 filter blur-[60px]"></div>
        </div>

        <div className="max-w-3xl">
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold mb-6 tracking-tight">
            <span className="text-white">Shop globally with </span>
            <span className="text-cartunlock-blue">American IPs</span>
          </h1>
          
          <p className="text-lg md:text-xl text-muted-foreground mb-8 max-w-lg">
            Access exclusive US shopping sites and unlock better prices with our secure proxy sessions.
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4">
            <Button 
              asChild 
              className="bg-cartunlock-blue hover:bg-cartunlock-blue/90 text-white px-8 py-6 text-lg rounded-xl glow-effect"
            >
              <Link to="/login">
                Get Started
                <ArrowRight size={18} className="ml-2" />
              </Link>
            </Button>
            
            <Button 
              asChild 
              variant="outline" 
              className="border-white/20 hover:bg-white/5 text-lg rounded-xl py-6"
            >
              <a href="#how-it-works">
                How It Works
              </a>
            </Button>
          </div>
          
          <div className="mt-12 flex items-center gap-4">
            <div className="flex -space-x-2">
              {[1, 2, 3, 4].map((i) => (
                <div 
                  key={i}
                  className="w-8 h-8 rounded-full border-2 border-cartunlock-background" 
                  style={{ 
                    backgroundColor: `hsl(${210 + i * 30}, 70%, 50%)`,
                  }}
                />
              ))}
            </div>
            <p className="text-sm text-muted-foreground">
              <span className="font-medium text-white">1,000+</span> active users this month
            </p>
          </div>
        </div>
      </section>
      
      {/* How It Works */}
      <section 
        id="how-it-works"
        ref={howItWorksRef}
        className="py-16 md:py-24 px-4 md:px-12 lg:px-24 opacity-0"
      >
        <div className="max-w-7xl mx-auto">
          <h2 className="text-3xl md:text-4xl font-bold mb-12 text-center">
            How <span className="text-cartunlock-blue">CartUnlock</span> Works
          </h2>
          
          <div className="grid md:grid-cols-3 gap-8">
            <FeatureCard 
              title="Choose Your Session"
              description="Select the session length that fits your shopping needs, from quick browsing to extended checkout time."
              icon={<Clock size={32} className="text-cartunlock-blue" />}
              step={1}
            />
            
            <FeatureCard 
              title="Access US-Based Sites"
              description="Browse US-exclusive stores with our secure proxy that makes it seem like you're shopping from America."
              icon={<Globe size={32} className="text-cartunlock-green" />}
              step={2}
            />
            
            <FeatureCard 
              title="Shop Securely"
              description="Your connection is encrypted and your personal data stays private throughout your shopping experience."
              icon={<Shield size={32} className="text-cartunlock-gold" />}
              step={3}
            />
          </div>
        </div>
      </section>
      
      {/* Compatible Sites */}
      <section 
        ref={sitesRef}
        className="py-16 md:py-24 px-4 md:px-12 lg:px-24 opacity-0"
      >
        <div className="max-w-7xl mx-auto">
          <h2 className="text-3xl md:text-4xl font-bold mb-4 text-center">
            Access Popular US Retailers
          </h2>
          <p className="text-lg text-muted-foreground mb-12 text-center max-w-2xl mx-auto">
            Shop on these sites and many more with our US-based proxy sessions
          </p>
          
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 md:gap-8">
            {['Amazon', 'Best Buy', 'Walmart', 'Target', 'Newegg', 'B&H Photo', 'Nike', 'Apple'].map((site) => (
              <div 
                key={site} 
                className="aspect-video rounded-xl glass-card hover-card flex items-center justify-center p-6"
              >
                <div className="text-center">
                  <ShoppingCart size={32} className="mb-4 mx-auto text-cartunlock-text/70" />
                  <p className="font-medium">{site}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
      
      {/* CTA Section */}
      <section className="py-16 md:py-24 px-4 md:px-12 lg:px-24">
        <div className="max-w-5xl mx-auto glass-card rounded-2xl p-8 md:p-12 relative overflow-hidden">
          <div className="absolute top-0 right-0 w-64 h-64 bg-cartunlock-blue opacity-10 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2"></div>
          <div className="absolute bottom-0 left-0 w-64 h-64 bg-cartunlock-green opacity-10 rounded-full blur-3xl translate-y-1/2 -translate-x-1/2"></div>
          
          <div className="relative z-10">
            <h2 className="text-3xl md:text-4xl font-bold mb-4 text-center">
              Ready to unlock global shopping?
            </h2>
            <p className="text-lg text-muted-foreground mb-8 text-center max-w-2xl mx-auto">
              Start browsing US stores with a secure proxy session in minutes.
            </p>
            
            <div className="flex flex-col sm:flex-row justify-center gap-4">
              <Button 
                asChild 
                size="lg"
                className="bg-cartunlock-blue hover:bg-cartunlock-blue/90 text-white px-8 py-6 text-lg rounded-xl"
              >
                <Link to="/login">
                  Create Account
                </Link>
              </Button>
              
              <Button 
                asChild 
                variant="outline"
                size="lg"
                className="border-white/20 hover:bg-white/5 text-lg rounded-xl py-6"
              >
                <Link to="/login">
                  Log In
                </Link>
              </Button>
            </div>
          </div>
        </div>
      </section>
    </PageLayout>
  );
};

interface FeatureCardProps {
  title: string;
  description: string;
  icon: React.ReactNode;
  step: number;
}

const FeatureCard: React.FC<FeatureCardProps> = ({ title, description, icon, step }) => {
  return (
    <div className="glass-card rounded-xl p-6 hover-card">
      <div className="h-12 w-12 rounded-full flex items-center justify-center neomorphic mb-4">
        {icon}
      </div>
      
      <div className="w-8 h-8 bg-cartunlock-blue/10 rounded-full flex items-center justify-center text-sm border border-cartunlock-blue/20 mb-4">
        {step}
      </div>
      
      <h3 className="text-xl font-semibold mb-3">{title}</h3>
      <p className="text-muted-foreground">{description}</p>
    </div>
  );
};

export default HomePage;
