import React from "react"
import { motion } from "framer-motion"
import { ShoppingCart, Clock, Shield, Play } from "lucide-react"
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip"
import { createSession, SessionType } from "@/services/sessionService"
import { useToast } from "@/hooks/use-toast"

interface SessionCardProps {
  type: SessionType
  title: string
  description: string
  hours: number
  isPopular?: boolean
  tooltipContent: string
  onClick?: () => Promise<void>
  disabled?: boolean
}

const SessionCard: React.FC<SessionCardProps> = ({
  type,
  title,
  description,
  hours,
  isPopular = false,
  tooltipContent,
  onClick,
  disabled = false,
}) => {
  const { toast } = useToast()

  const IconComponent = {
    quick: ShoppingCart,
    extended: Clock,
    premium: Shield,
  }[type]

  const handleStartSession = async () => {
    if (onClick) return onClick()

    toast({
      title: "Creating session...",
      description: "Please wait while we set up your US shopping session",
    })

    const sessionUrl = await createSession(type)

    if (sessionUrl) {
      toast({
        title: "Session ready!",
        description: "Your US shopping session is now active",
      })
      window.open(sessionUrl, "_blank")
    }
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 25 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ scale: 1.015 }}
      transition={{ duration: 0.4, ease: "easeOut" }}
    >
      <Card
        className={`relative backdrop-blur-md bg-white/5 border border-white/10 shadow-lg rounded-2xl transition-all hover:shadow-xl ${
          isPopular ? "ring-2 ring-cartunlock-gold/40" : ""
        }`}
      >
        {isPopular && (
          <div className="absolute top-3 right-3 text-xs bg-cartunlock-gold/90 text-cartunlock-background px-2 py-1 rounded-full font-semibold shadow">
            POPULAR
          </div>
        )}

        <CardHeader>
          <CardTitle className="flex items-center gap-2 text-white text-lg">
            <div className="h-9 w-9 flex items-center justify-center rounded-full bg-cartunlock-blue/20">
              <IconComponent size={18} className="text-cartunlock-blue" />
            </div>
            {title}
          </CardTitle>
          <CardDescription className="text-sm text-white/70 mt-1">
            {description}
          </CardDescription>
        </CardHeader>

        <CardContent className="text-sm text-white/80 space-y-3">
          <div className="flex justify-between">
            <span className="text-muted-foreground">Session length</span>
            <span className="font-medium">{hours} hour{hours > 1 ? "s" : ""}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-muted-foreground">Location</span>
            <span className="font-medium">United States</span>
          </div>
          <div className="flex justify-between">
            <span className="text-muted-foreground">IP protection</span>
            <span className={`font-medium ${type === "premium" ? "text-cartunlock-green" : "text-white"}`}>
              {type === "premium" ? "Enhanced" : "Active"}
            </span>
          </div>
        </CardContent>

        <CardFooter>
          <TooltipProvider>
            <Tooltip>
              <TooltipTrigger asChild>
                <Button
                  onClick={handleStartSession}
                  disabled={disabled}
                  className="w-full bg-cartunlock-blue hover:bg-cartunlock-blue/90 transition-all duration-200 transform hover:-translate-y-[2px] hover:shadow-lg"
                >
                  <Play size={16} />
                  <span className="ml-2">Start Session</span>
                </Button>
              </TooltipTrigger>
              <TooltipContent className="bg-background text-white border border-white/10">
                <p>{tooltipContent}</p>
              </TooltipContent>
            </Tooltip>
          </TooltipProvider>
        </CardFooter>
      </Card>
    </motion.div>
  )
}

export default SessionCard