"use client"

import React, { useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { supabase } from "@/integrations/supabase/client"
import { toast } from "@/hooks/use-toast"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Checkbox } from "@/components/ui/checkbox"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Lock, Unlock, Mail, Key } from "lucide-react"
import PageLayout from "@/components/layout/PageLayout"

const LoginPage: React.FC = () => {
  const [isUnlocking, setIsUnlocking] = useState(false)
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [registerEmail, setRegisterEmail] = useState("")
  const [registerPassword, setRegisterPassword] = useState("")
  const [confirmPassword, setConfirmPassword] = useState("")
  const navigate = useNavigate()

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsUnlocking(true)

    const { error } = await supabase.auth.signInWithPassword({
      email,
      password,
    })

    if (error) {
      toast({
        title: "Erro ao fazer login",
        description: error.message,
        variant: "destructive",
      })
      setIsUnlocking(false)
      return
    }

    toast({ title: "Login realizado!", description: "Redirecionando..." })

    setTimeout(() => {
      window.location.href = "/dashboard"
    }, 1500)
  }

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault()

    if (registerPassword !== confirmPassword) {
      toast({
        title: "As senhas não coincidem",
        description: "Verifique e tente novamente",
        variant: "destructive",
      })
      return
    }

    const { error } = await supabase.auth.signUp({
      email: registerEmail,
      password: registerPassword,
    })

    if (error) {
      toast({
        title: "Erro ao criar conta",
        description: error.message,
        variant: "destructive",
      })
      return
    }

    toast({
      title: "Conta criada!",
      description: "Verifique seu e-mail para confirmar.",
    })
  }

  return (
    <PageLayout withFooter={false}>
      <div className="min-h-[calc(100vh-80px)] flex flex-col items-center justify-center relative">
        {/* Background Elements */}
        <div className="absolute top-0 left-0 right-0 h-full w-full overflow-hidden z-[-1]">
          <div className="absolute top-[20%] left-[10%] w-72 h-72 rounded-full bg-cartunlock-blue opacity-5 filter blur-[80px] animate-pulse"></div>
          <div className="absolute bottom-[20%] right-[10%] w-72 h-72 rounded-full bg-cartunlock-green opacity-5 filter blur-[80px] animate-pulse" style={{ animationDelay: "1s" }}></div>
        </div>

        <div className="w-full max-w-md">
          <div className="mb-8 text-center">
            <div className="flex justify-center mb-4">
              <div
                className={`
                relative h-16 w-16 rounded-xl flex items-center justify-center
                transition-all duration-500
                ${isUnlocking ? "bg-cartunlock-green/20" : "bg-cartunlock-blue/20"}
              `}
              >
                <div
                  className={`
                  absolute inset-0 rounded-xl
                  transition-all duration-1000 
                  ${isUnlocking ? "opacity-100" : "opacity-0"}
                `}
                >
                  <div className="absolute inset-0 animate-pulse-glow rounded-xl"></div>
                </div>

                {isUnlocking ? (
                  <Unlock size={32} className="text-cartunlock-green animate-fade-in" />
                ) : (
                  <Lock size={32} className="text-cartunlock-blue" />
                )}
              </div>
            </div>

            <h1 className="text-3xl font-bold text-white">Welcome to CartUnlock</h1>
            <p className="text-muted-foreground mt-2">Sign in to access US shopping sessions</p>
          </div>

          <div className="glass-card rounded-xl p-6 backdrop-blur-lg border border-white/10 shadow-xl">
            <Tabs defaultValue="login" className="w-full">
              <TabsList className="grid grid-cols-2 mb-6">
                <TabsTrigger value="login">Login</TabsTrigger>
                <TabsTrigger value="register">Register</TabsTrigger>
              </TabsList>

              {/* Login Tab */}
              <TabsContent value="login">
                <form onSubmit={handleLogin} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="email">Email</Label>
                    <div className="relative">
                      <Mail size={16} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground" />
                      <Input
                        id="email"
                        type="email"
                        placeholder="your@email.com"
                        className="pl-10 bg-background/50"
                        required
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <Label htmlFor="password">Password</Label>
                      <Link to="/forgot-password" className="text-xs text-cartunlock-blue hover:underline">
                        Forgot password?
                      </Link>
                    </div>
                    <div className="relative">
                      <Key size={16} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground" />
                      <Input
                        id="password"
                        type="password"
                        placeholder="••••••••"
                        className="pl-10 bg-background/50"
                        required
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                      />
                    </div>
                  </div>

                  <div className="flex items-center space-x-2">
                    <Checkbox id="remember" />
                    <label htmlFor="remember" className="text-sm">
                      Remember me
                    </label>
                  </div>

                  <Button
                    type="submit"
                    className={`w-full py-6 rounded-lg transition-all ${
                      isUnlocking
                        ? "bg-cartunlock-green hover:bg-cartunlock-green/90"
                        : "bg-cartunlock-blue hover:bg-cartunlock-blue/90"
                    }`}
                  >
                    {isUnlocking ? "Unlocking Access..." : "Sign In"}
                  </Button>
                </form>
              </TabsContent>

              {/* Register Tab */}
              <TabsContent value="register">
                <form onSubmit={handleRegister} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="register-email">Email</Label>
                    <div className="relative">
                      <Mail size={16} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground" />
                      <Input
                        id="register-email"
                        type="email"
                        placeholder="your@email.com"
                        className="pl-10 bg-background/50"
                        required
                        value={registerEmail}
                        onChange={(e) => setRegisterEmail(e.target.value)}
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="register-password">Password</Label>
                    <div className="relative">
                      <Key size={16} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground" />
                      <Input
                        id="register-password"
                        type="password"
                        placeholder="••••••••"
                        className="pl-10 bg-background/50"
                        required
                        value={registerPassword}
                        onChange={(e) => setRegisterPassword(e.target.value)}
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="confirm-password">Confirm Password</Label>
                    <div className="relative">
                      <Key size={16} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground" />
                      <Input
                        id="confirm-password"
                        type="password"
                        placeholder="••••••••"
                        className="pl-10 bg-background/50"
                        required
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                      />
                    </div>
                  </div>

                  <div className="flex items-center space-x-2">
                    <Checkbox id="terms" required />
                    <label htmlFor="terms" className="text-sm">
                      I agree to the{" "}
                      <Link to="/terms" className="text-cartunlock-blue hover:underline">
                        Terms of Service
                      </Link>{" "}
                      and{" "}
                      <Link to="/privacy" className="text-cartunlock-blue hover:underline">
                        Privacy Policy
                      </Link>
                    </label>
                  </div>

                  <Button type="submit" className="w-full py-6 rounded-lg bg-cartunlock-blue hover:bg-cartunlock-blue/90">
                    Create Account
                  </Button>
                </form>
              </TabsContent>
            </Tabs>
          </div>
        </div>
      </div>
    </PageLayout>
  )
}

export default LoginPage
