import React from 'react';
import { motion } from "framer-motion";
import { ArrowRight, ShoppingCart } from "lucide-react";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { useQuery } from "@tanstack/react-query";
import { getUserSessions, SessionData } from "@/services/sessionService";

interface ActivityItem {
  id: string;
  title: string;
  time: string;
  status: string;
  statusColor: string;
}

const formatTimeDifference = (date: string) => {
  const now = new Date();
  const pastDate = new Date(date);
  const diffMs = now.getTime() - pastDate.getTime();
  const diffMins = Math.round(diffMs / 60000);
  const diffHours = Math.round(diffMs / 3600000);
  const diffDays = Math.round(diffMs / 86400000);
  
  if (diffMins < 60) return `${diffMins} minutes ago`;
  if (diffHours < 24) return `${diffHours} hours ago`;
  return `${diffDays} days ago`;
};

const RecentActivity: React.FC = () => {
  const { data: sessions = [], isLoading } = useQuery({
    queryKey: ['recentSessions'],
    queryFn: getUserSessions,
    refetchInterval: 30000, // Refetch every 30 seconds
  });
  
  const activities: ActivityItem[] = React.useMemo(() => {
    if (!sessions || sessions.length === 0) {
      return [
        { 
          id: "default-1",
          title: "Amazon.com session", 
          time: "2 hours ago", 
          status: "Completed",
          statusColor: "text-cartunlock-green" 
        },
        { 
          id: "default-2",
          title: "BestBuy.com session", 
          time: "Yesterday", 
          status: "Completed",
          statusColor: "text-cartunlock-green" 
        },
        { 
          id: "default-3",
          title: "Walmart.com session", 
          time: "3 days ago", 
          status: "Error",
          statusColor: "text-cartunlock-error" 
        },
      ];
    }
    
    return sessions.map(session => ({
      id: session.id,
      title: `${session.site} session`,
      time: formatTimeDifference(session.created_at),
      status: session.status === 'active' ? 'Active' : 
              session.status === 'expired' ? 'Expired' : 
              session.status === 'error' ? 'Error' : 'Completed',
      statusColor: session.status === 'active' ? 'text-cartunlock-green' :
                  session.status === 'error' ? 'text-cartunlock-error' :
                  session.status === 'expired' ? 'text-amber-500' : 'text-cartunlock-green'
    }));
  }, [sessions]);

  const container = {
    hidden: { opacity: 0 },
    show: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };
  
  const item = {
    hidden: { opacity: 0, y: 20 },
    show: { opacity: 1, y: 0 }
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: 0.3 }}
    >
      <Card className="glass-card">
        <CardHeader>
          <CardTitle>Recent Activity</CardTitle>
          <CardDescription>Your recent sessions and activity</CardDescription>
        </CardHeader>
        <CardContent>
          <motion.div 
            className="space-y-4"
            variants={container}
            initial="hidden"
            animate="show"
          >
            {activities.map((activity, index) => (
              <motion.div 
                key={activity.id}
                variants={item}
                className="flex items-center justify-between p-3 rounded-lg bg-white/5 hover:bg-white/10 transition-colors"
              >
                <div className="flex items-center">
                  <div className="h-8 w-8 rounded-full bg-cartunlock-background flex items-center justify-center mr-3">
                    <ShoppingCart size={16} className="text-cartunlock-blue" />
                  </div>
                  <div>
                    <p className="font-medium">{activity.title}</p>
                    <p className="text-xs text-muted-foreground">{activity.time}</p>
                  </div>
                </div>
                <span className={`text-sm font-medium ${activity.statusColor}`}>
                  {activity.status}
                </span>
              </motion.div>
            ))}
          </motion.div>
        </CardContent>
        <CardFooter>
          <Button 
            variant="outline" 
            asChild 
            className="w-full border-white/10 hover:bg-white/5 hover:-translate-y-1 transition-transform"
          >
            <a href="/sessions">
              View All Sessions
              <ArrowRight size={14} className="ml-2" />
            </a>
          </Button>
        </CardFooter>
      </Card>
    </motion.div>
  );
};

export default RecentActivity;
