import React, { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import { supabase } from "@/integrations/supabase/client";
import { Loader2, Timer, AlertTriangle, ExternalLink, ArrowLeft } from "lucide-react";
import { Button } from "@/components/ui/button";
import { isBefore, formatDuration, intervalToDuration } from "date-fns";
import { toast } from "@/hooks/use-toast";

interface Session {
  session_url: string;
  expires_at: string;
}

const PublicSessionPage: React.FC = () => {
  const { slug } = useParams();
  const [session, setSession] = useState<Session | null>(null);
  const [status, setStatus] = useState<"loading" | "not_found" | "expired" | "valid">("loading");
  const [remainingTime, setRemainingTime] = useState<string>("");

  useEffect(() => {
    if (!slug) return;

    const fetchSession = async () => {
      try {
        const { data, error } = await supabase
          .from("sessions")
          .select("*")
          .ilike("session_url", `%${slug}%`)
          .single();

        if (error || !data) {
          setStatus("not_found");
          return;
        }

        const isExpired = isBefore(new Date(data.expires_at), new Date());
        setSession(data);
        setStatus(isExpired ? "expired" : "valid");
      } catch (error) {
        console.error("Erro ao buscar sessão:", error);
        toast({
          title: "Erro",
          description: "Não foi possível carregar a sessão",
          variant: "destructive",
        });
        setStatus("not_found");
      }
    };

    fetchSession();
  }, [slug]);

  // Live countdown timer
  useEffect(() => {
    if (!session || status !== "valid") return;

    const interval = setInterval(() => {
      const now = new Date();
      const expires = new Date(session.expires_at);

      if (isBefore(expires, now)) {
        setStatus("expired");
        clearInterval(interval);
        return;
      }

      const duration = intervalToDuration({ start: now, end: expires });
      setRemainingTime(
        formatDuration(duration, { format: ["hours", "minutes", "seconds"] })
      );
    }, 1000);

    return () => clearInterval(interval);
  }, [session, status]);

  const handleOpenSession = () => {
    if (session?.session_url?.startsWith("http")) {
      window.open(session.session_url, "_blank");
    }
  };

  const renderLogo = () => (
    <div className="flex items-center gap-2 mb-12">
      <div className="relative h-10 w-10 animate-pulse-glow">
        <div className="absolute inset-0 rounded-full bg-cartunlock-blue opacity-20"></div>
        <div className="absolute inset-1 rounded-full bg-cartunlock-background flex items-center justify-center">
          <div className="h-4 w-4 rounded-full bg-cartunlock-blue"></div>
        </div>
      </div>
      <span className="text-2xl font-bold text-white tracking-tight">
        Cart<span className="text-cartunlock-blue">Unlock</span>
      </span>
    </div>
  );

  const renderStatus = () => {
    if (status === "loading") {
      return (
        <div className="flex flex-col items-center justify-center py-24 text-center">
          <Loader2 className="animate-spin w-10 h-10 mb-6 text-cartunlock-blue" />
          <p className="text-lg text-muted-foreground">Carregando sessão...</p>
        </div>
      );
    }

    if (status === "not_found") {
      return (
        <div className="text-center py-16">
          <AlertTriangle className="w-16 h-16 text-cartunlock-error mx-auto mb-6" />
          <h2 className="text-3xl font-bold mb-4">Sessão não encontrada</h2>
          <p className="text-xl text-muted-foreground mb-8">
            Esta sessão não existe ou pode ter sido excluída.
          </p>
          <Button asChild size="lg" className="mt-4">
            <Link to="/">
              <ArrowLeft className="mr-2" size={18} />
              Voltar para página inicial
            </Link>
          </Button>
        </div>
      );
    }

    if (status === "expired") {
      return (
        <div className="text-center py-16">
          <Timer className="w-16 h-16 text-cartunlock-gold mx-auto mb-6" />
          <h2 className="text-3xl font-bold mb-4">Sessão expirada</h2>
          <p className="text-xl text-muted-foreground mb-8">
            Esta sessão não está mais disponível.
          </p>
          <Button asChild size="lg" className="mt-4">
            <Link to="/">
              <ArrowLeft className="mr-2" size={18} />
              Voltar para página inicial
            </Link>
          </Button>
        </div>
      );
    }

    return (
      <div className="text-center py-16 space-y-8">
        <h1 className="text-3xl sm:text-4xl font-bold bg-gradient-to-r from-white to-white/70 bg-clip-text text-transparent">
          Sessão Ativa
        </h1>

        <div className="glass-card p-8 mx-auto max-w-md">
          <p className="text-lg mb-6">
            Expira em {" "}
            <span className="font-semibold text-cartunlock-blue">{remainingTime}</span>
          </p>

          <Button
            size="lg"
            className="bg-cartunlock-blue hover:bg-cartunlock-blue/90 flex items-center gap-2 text-lg w-full justify-center"
            onClick={handleOpenSession}
          >
            <ExternalLink size={20} />
            Abrir Sessão
          </Button>
        </div>
      </div>
    );
  };

  return (
    <div className="min-h-screen flex flex-col bg-cartunlock-background">
      <div className="flex-1 container px-4 py-8 flex flex-col items-center justify-center">
        {renderLogo()}
        {renderStatus()}
      </div>

      <footer className="w-full py-6 border-t border-white/10">
        <div className="container px-4 text-center text-sm text-muted-foreground">
          © {new Date().getFullYear()} CartUnlock. Todos os direitos reservados.
        </div>
      </footer>
    </div>
  );
};

export default PublicSessionPage;