
import React from 'react';
import { motion } from "framer-motion";
import { AlertCircle } from "lucide-react";

interface UsageBannerProps {
  showBanner: boolean;
}

const UsageBanner: React.FC<UsageBannerProps> = ({ showBanner }) => {
  if (!showBanner) return null;

  return (
    <motion.div
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="w-full mb-6 p-4 bg-amber-500/10 border border-amber-500/20 rounded-lg flex items-center"
    >
      <div className="h-8 w-8 rounded-full bg-amber-500/20 flex items-center justify-center mr-3">
        <AlertCircle size={16} className="text-amber-500" />
      </div>
      <div>
        <h4 className="font-semibold text-amber-300">Usage Limit Warning</h4>
        <p className="text-sm text-amber-200/90">
          You've nearly reached your monthly session limit. Consider upgrading your plan to get more sessions.
        </p>
      </div>
    </motion.div>
  );
};

export default UsageBanner;
