import { supabase } from "@/integrations/supabase/client";
import { toast } from "@/hooks/use-toast";

export type SessionType = "quick" | "extended" | "premium";

export interface SessionData {
  id: string;
  user_id: string;
  site: string;
  status: string;
  created_at: string;
  expires_at: string | null;
  session_url: string | null;
  proxy_region: string | null;
}

export interface SessionUsage {
  quick: { used: number; total: number };
  extended: { used: number; total: number };
  premium: { used: number; total: number };
}

const BACKEND_URL = "https://expressjs-production-7cac.up.railway.app/create-session";

export const createSession = async (sessionType: SessionType): Promise<string | null> => {
  try {
    const { data: userData } = await supabase.auth.getUser();
    const user = userData?.user;

    if (!user) {
      toast({
        title: "Usuário não autenticado",
        description: "Faça login para iniciar uma sessão",
        variant: "destructive",
      });
      return null;
    }

    const response = await fetch(BACKEND_URL, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        user_id: user.id,
        site: sessionType,
      }),
    });

    const result = await response.json();

    if (!response.ok) {
      toast({
        title: "Erro ao criar sessão",
        description: result.error || "Erro inesperado",
        variant: "destructive",
      });
      return null;
    }

    if (result.session_url) {
      return result.session_url;
    } else {
      toast({
        title: "Erro ao iniciar sessão",
        description: "Resposta inválida do servidor",
        variant: "destructive",
      });
      return null;
    }
  } catch (error) {
    console.error("Erro inesperado:", error);
    toast({
      title: "Error starting session",
      description: "An unexpected error occurred",
      variant: "destructive",
    });
    return null;
  }
};

export const getUserSessions = async (): Promise<SessionData[]> => {
  try {
    const { data: userData } = await supabase.auth.getUser();
    const user = userData?.user;

    if (!user) return [];

    const { data, error } = await supabase
      .from("sessions")
      .select("*")
      .eq("user_id", user.id)
      .order("created_at", { ascending: false })
      .limit(3);

    if (error) {
      console.error("Erro ao buscar sessões:", error);
      return [];
    }

    return data as SessionData[];
  } catch (error) {
    console.error("Erro inesperado:", error);
    return [];
  }
};

export const getUserSessionUsage = async (): Promise<SessionUsage> => {
  try {
    const { data: userData } = await supabase.auth.getUser();
    const user = userData?.user;

    if (!user) {
      return {
        quick: { used: 0, total: 5 },
        extended: { used: 0, total: 3 },
        premium: { used: 0, total: 1 },
      };
    }

    const [quick, extended, premium] = await Promise.all([
      supabase.from("sessions").select("*", { count: "exact" }).eq("user_id", user.id).eq("site", "quick"),
      supabase.from("sessions").select("*", { count: "exact" }).eq("user_id", user.id).eq("site", "extended"),
      supabase.from("sessions").select("*", { count: "exact" }).eq("user_id", user.id).eq("site", "premium"),
    ]);

    return {
      quick: { used: quick.data?.length || 0, total: 5 },
      extended: { used: extended.data?.length || 0, total: 3 },
      premium: { used: premium.data?.length || 0, total: 1 },
    };
  } catch (error) {
    console.error("Erro ao calcular uso de sessões:", error);
    return {
      quick: { used: 0, total: 5 },
      extended: { used: 0, total: 3 },
      premium: { used: 0, total: 1 },
    };
  }
};
