"use client"

import React, { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import { supabase } from "@/integrations/supabase/client"
import PageLayout from "@/components/layout/PageLayout"
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Search, Calendar, ArrowUpDown, Globe, Clock, ShieldCheck, CheckCircle, XCircle, AlertCircle } from "lucide-react"
import { formatDistanceToNowStrict, isBefore } from "date-fns"

const SessionsPage: React.FC = () => {
  const [sessions, setSessions] = useState<any[]>([])
  const [filter, setFilter] = useState("")
  const navigate = useNavigate()

  useEffect(() => {
    const fetchSessions = async () => {
      const { data } = await supabase
        .from("sessions")
        .select("*")
        .order("created_at", { ascending: false })

      setSessions(data || [])
    }

    fetchSessions()
  }, [])

  const activeSessions = sessions.filter((s) => isBefore(new Date(), new Date(s.expires_at)))
  const historySessions = sessions.filter((s) => isBefore(new Date(s.expires_at), new Date()))

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "active":
        return <Badge className="bg-cartunlock-blue">Active</Badge>
      case "completed":
        return <Badge className="bg-cartunlock-green">Completed</Badge>
      case "error":
        return <Badge className="bg-cartunlock-error">Error</Badge>
      default:
        return <Badge className="bg-muted">Unknown</Badge>
    }
  }

  return (
    <PageLayout>
      <div className="mb-8">
        <h1 className="text-3xl font-bold tracking-tight">Sessions</h1>
        <p className="text-muted-foreground mt-2">View your active and past shopping sessions</p>
      </div>

      <div className="mb-6">
        <div className="flex items-center gap-4">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground h-4 w-4" />
            <Input
              placeholder="Search sessions..."
              className="pl-10"
              value={filter}
              onChange={(e) => setFilter(e.target.value)}
            />
          </div>

          <Button variant="outline" className="border-white/10 hover:bg-white/5">
            <Calendar size={16} className="mr-2" />
            Date Range
          </Button>

          <Button variant="outline" className="border-white/10 hover:bg-white/5">
            <ArrowUpDown size={16} className="mr-2" />
            Sort
          </Button>
        </div>
      </div>

      <Tabs defaultValue="active" className="w-full">
        <TabsList className="mb-6">
          <TabsTrigger value="active">Active</TabsTrigger>
          <TabsTrigger value="history">History</TabsTrigger>
        </TabsList>

        <TabsContent value="active" className="space-y-4">
          {activeSessions.length > 0 ? (
            activeSessions.map((session) => (
              <Card key={session.id} className="glass-card hover-card">
                <CardHeader className="pb-2">
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-lg flex items-center">
                        <Globe size={18} className="mr-2 text-cartunlock-blue" />
                        {session.site}
                      </CardTitle>
                      <CardDescription>
                        Started {formatDistanceToNowStrict(new Date(session.created_at))} ago
                      </CardDescription>
                    </div>
                    {getStatusBadge("active")}
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div className="flex flex-col">
                      <span className="text-sm text-muted-foreground mb-1">Session Duration</span>
                      <span className="font-medium flex items-center">
                        <Clock size={16} className="mr-2 text-cartunlock-blue" />
                        {formatDistanceToNowStrict(new Date(session.expires_at))} remaining
                      </span>
                    </div>
                    <div className="flex flex-col">
                      <span className="text-sm text-muted-foreground mb-1">Session Link</span>
                      <span className="font-medium text-sm truncate text-cartunlock-blue">
                        {session.session_url.replace("https://session.cartunlock.com/", "")}
                      </span>
                    </div>
                    <div className="flex flex-col">
                      <span className="text-sm text-muted-foreground mb-1">IP Protection</span>
                      <span className="font-medium flex items-center">
                        <ShieldCheck size={16} className="mr-2 text-cartunlock-blue" />
                        {session.proxy_region || "US"}
                      </span>
                    </div>
                  </div>

                  <div className="mt-4 flex gap-3">
                    <Button
                      className="bg-cartunlock-blue hover:bg-cartunlock-blue/80"
                      size="sm"
                      onClick={() => navigate(`/session/${session.session_url.split("/").pop()}`)}
                    >
                      Open Session
                    </Button>
                    <Button variant="outline" size="sm" className="border-white/10 hover:bg-white/5">
                      End Session
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))
          ) : (
            <div className="text-center py-12">
              <div className="h-12 w-12 rounded-full bg-muted flex items-center justify-center mx-auto mb-4">
                <Globe size={24} className="text-muted-foreground" />
              </div>
              <h3 className="text-lg font-medium mb-1">No Active Sessions</h3>
              <p className="text-muted-foreground mb-6">
                You don't have any active browsing sessions right now.
              </p>
              <Button className="bg-cartunlock-blue hover:bg-cartunlock-blue/80">
                Start a New Session
              </Button>
            </div>
          )}
        </TabsContent>

        <TabsContent value="history" className="space-y-4">
          {historySessions.length > 0 ? (
            historySessions.map((session) => (
              <Card key={session.id} className="glass-card">
                <CardContent className="p-4">
                  <div className="flex justify-between items-center">
                    <div className="flex items-center">
                      <div className="h-10 w-10 rounded-full bg-white/5 flex items-center justify-center mr-4">
                        <CheckCircle size={20} className="text-cartunlock-green" />
                      </div>
                      <div>
                        <h3 className="font-medium">{session.site}</h3>
                        <p className="text-sm text-muted-foreground">
                          {new Date(session.created_at).toLocaleString()}
                        </p>
                      </div>
                    </div>
                    {getStatusBadge("completed")}
                  </div>
                </CardContent>
              </Card>
            ))
          ) : (
            <div className="text-center py-12">
              <div className="h-12 w-12 rounded-full bg-muted flex items-center justify-center mx-auto mb-4">
                <Search size={24} className="text-muted-foreground" />
              </div>
              <h3 className="text-lg font-medium mb-1">No Results Found</h3>
              <p className="text-muted-foreground">
                We couldn't find any sessions matching your search.
              </p>
            </div>
          )}
        </TabsContent>
      </Tabs>
    </PageLayout>
  )
}

export default SessionsPage
