
import React from 'react';
import { motion } from "framer-motion";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Button } from "@/components/ui/button";
import { useQuery } from "@tanstack/react-query";
import { getUserSessionUsage, SessionUsage } from "@/services/sessionService";

const UsageStats: React.FC = () => {
  const { data: usageData, isLoading } = useQuery({
    queryKey: ['sessionUsage'],
    queryFn: getUserSessionUsage,
    refetchInterval: 30000, // Refetch every 30 seconds
  });
  
  const defaultData: SessionUsage = {
    quick: { used: 0, total: 5 },
    extended: { used: 0, total: 3 },
    premium: { used: 0, total: 1 }
  };
  
  const data = usageData || defaultData;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: 0.2 }}
    >
      <Card className="glass-card">
        <CardHeader>
          <CardTitle>Session Usage</CardTitle>
          <CardDescription>Your session usage this month</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between text-sm mb-2">
                <span>Quick Sessions</span>
                <span>{data.quick.used}/{data.quick.total} used</span>
              </div>
              <Progress value={(data.quick.used / data.quick.total) * 100} className="h-2" />
            </div>
            
            <div>
              <div className="flex justify-between text-sm mb-2">
                <span>Extended Sessions</span>
                <span>{data.extended.used}/{data.extended.total} used</span>
              </div>
              <Progress value={(data.extended.used / data.extended.total) * 100} className="h-2" />
            </div>
            
            <div>
              <div className="flex justify-between text-sm mb-2">
                <span>Premium Sessions</span>
                <span>{data.premium.used}/{data.premium.total} used</span>
              </div>
              <Progress value={(data.premium.used / data.premium.total) * 100} className="h-2" />
            </div>
          </div>
        </CardContent>
        <CardFooter>
          <Button 
            variant="outline" 
            className="w-full border-white/10 hover:bg-white/5 hover:-translate-y-1 transition-transform"
          >
            View Usage Details
          </Button>
        </CardFooter>
      </Card>
    </motion.div>
  );
};

export default UsageStats;
